import {observable,action} from 'mobx';

class Loaging {
    constructor(store){
        this.store = store;
    }
    @observable bLoading = false
    @action showLoading = () => {
        this.bLoading = true;
    }
    @action hidLoading = () => {
        this.bLoading = false;
    }
}

export default Loaging;



