import React,{Component} from 'react';
import Banner from './../components/Banner'
import Nav from './../components/Nav'
import Cell from './../components/Cell/Cell'
import {Link} from 'react-router-dom'
import {inject,observer} from 'mobx-react'

@inject('store')
@observer
class Home extends Component {
    state={
        banner:[],
        home:[],
    }
   
    componentDidMount(){
        let {getdata} = this.props.store.getdata;
        getdata("/mumu/home")
        
    }
    
    render (){
        let bLoading = this.props.store.loading.bLoading
        let data = this.props.store.getdata.updata[0]
        if(data){
            var { banner,home } = data
        }
                
        return (
            <div className='home' style={{background:"#ececec"}}>
                    { !bLoading && <Banner banner={banner} />}
                    { !bLoading && <Nav />}
                    {
                        !bLoading && home && home.map((item,index)=>(
                            <Link to={'/detail/' + item.uniquekey } key={index} >
                                <Cell data={item} key={index} />
                            </Link>
                        ))
                    }
                    
            </div>
        )
    }
}

export default Home