
import {observable,action} from 'mobx';
import Axios from 'axios';



class Getdata {
    constructor(store){
        this.store = store
    }

    @observable updata = {
        auth:false,
        data:{}

    };

    @action getdata = (url) => {
        return Axios({
            url:url,
        }).then(res=>{
            this.updata = res.data.data
        })
    }
}
export default Getdata